.techDetail {
  &-relatedList, &-certsList {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    font-size: .8em;
    padding: .3em;
  }

  &-img {
    img {
      width: 3em;
      border-radius: 50%;
      background-color: white;
      display: block;
      margin: 0 auto;
    }
  }

  &-header {
    display: flex;
    flex-direction: column;
    align-items: center;

    p {
      font-size: .8em;
      margin-left: .5em;
      text-align: center;
      padding: .5em;
    }
  }

  section {
    padding: .5em;
    h3 {
      font-weight: bold;
      text-align: center;
    }
  }
}
